import store from '@/js/store.js'
import constant from '@/js/constant.js'
import api from '@/js/api.js'

const http = function(url, data, success, options) {
	
	uni.showLoading({
		title: '...',
		mask: true
	});

	var type = typeof data === 'function';
	if (type) {
		options = success;
		success = data;
		data = {};
	}

	options = options || {};

	return uni.request({
		url: constant.server_url + url,
		header: {
			Authorization: store.getToken(),
			uniapp: 'true'
		},
		data: data,
		method: options.type || 'post',
		// dataType: options.dataType || 'json',
		success: function(res) {
			
			uni.hideLoading();
			var code = res.data.code;
			var data = res.data.data;

			if (code === 0) {
				success && success(data);
			} else if (code === 399) {
				uni.showModal({
					content: data || '',
					showCancel: false,
					success: function(res) {
						uni.reLaunch({
							url: '/pages/login'
						})
					}
				});
			} else if (code === 401) {
				uni.showModal({
					content: data || ',',
					showCancel: false
				});
			} else {
				uni.showModal({
					content: data ||',',
					showCancel: false
				});
			}

		},
		fail: () => {
			
			uni.hideLoading();
			uni.showModal({
				content: '',
				showCancel: false
			});
		}

	})


}

const upload = function(file, data ,success) {
	uni.showLoading({
		title: '...',
		mask: true
	});

	uni.uploadFile({
		url: constant.server_url + api.upload_trip_img,
		filePath: file,
		header: {
			Authorization: store.getToken()
		},
		name: 'multipartFile',
		formData:data,
		success: (res) => {
			uni.hideLoading();
			success && success(JSON.parse(res.data));
		},
		fail: (res) => {
			uni.hideLoading();
		}
	});

}


export default {
	http,
	upload
}
