const dealNull = (value) => {

	if (!value) {
		return '0';
	} else {
		return value;
	}

}

const dealDate = (date) =>{
	let fmt = 'yyyy-MM-dd hh:mm:ss'
	
	var o = {
		"M+": date.getMonth() + 1, //
		"d+": date.getDate(), //
		"h+": date.getHours(), //
		"m+": date.getMinutes(), //
		"s+": date.getSeconds(), //
	};
	if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
	for (var k in o)
		if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" +
			o[k]).substr(("" + o[k]).length)));
	return fmt;
}

export default {
  dealNull,
  dealDate
}
